<template>
  <div class="results-container">
    <h1 style="color: black;">üèÜ Results</h1>

    <div
      v-for="category in [1, 2, 3]"
      :key="category"
      class="category-card"
    >
      <h2>Category {{ category }}</h2>

      <div
        v-if="results(category).length === 0"
        class="empty"
      >
        No entries
      </div>

      <ol v-else class="ranking">
        <li
          v-for="(costume, index) in results(category)"
          :key="costume.id"
          class="rank-item"
        >
          <span class="rank">
            {{ medal(index) }}
          </span>

          <img
            :src="costume.image"
            class="thumb"
            alt="Costume"
          />

          <div class="info">
            <div class="name">{{ costume.name }}</div>
            <div class="votes">{{ costume.votes }} votes</div>
          </div>
        </li>
      </ol>
    </div>
  </div>
</template>

<script>
import { useCompleteStore } from '@/store/store';

export default {
  name: 'ResultsView',

  computed: {
    store() {
      return useCompleteStore();
    }
  },

  mounted() {
    this.store.loadContestFromLocalStorage();
  },

  methods: {
    results(category) {
      return this.store.getRankedResults(category);
    },
    medal(index) {
      if (index === 0) return 'ü•á';
      if (index === 1) return 'ü•à';
      if (index === 2) return 'ü•â';
      return `#${index + 1}`;
    }
  }
};
</script>

<style scoped>
.results-container {
  padding: 16px;
  max-width: 520px;
  margin: 0 auto;
  color: white;
}

h1 {
  text-align: center;
  margin-bottom: 24px;
}

.category-card {
  background: #111;
  padding: 16px;
  border-radius: 16px;
  margin-bottom: 20px;
}

h2 {
  margin-bottom: 12px;
}

.empty {
  opacity: 0.6;
  font-style: italic;
}

.ranking {
  list-style: none;
  padding: 0;
  margin: 0;
}

.rank-item {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  background: #000;
  padding: 10px;
  border-radius: 12px;
}

.rank {
  font-size: 1.6rem;
  width: 40px;
  text-align: center;
}

.thumb {
  width: 56px;
  height: 56px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 12px;
}

.info {
  flex: 1;
}

.name {
  font-weight: 600;
  font-size: 1.1rem;
}

.votes {
  font-size: 0.9rem;
  opacity: 0.7;
}
</style>
